<template>
    <div class="main-page">
        <div class="headerTag">选择物料</div>
        <div class="persons">
            <div class="persons-line" v-for="(item,index) in materialList" :key="index" @click="chooseItem(item)">
                <p class="name">{{item.FName}}</p>
                <p class="orgname"> [ {{item.FNumber}} ]</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ChoosePerson",
    props: {},
    components: {},

    data() {
        return {
            materialList: []
        };
    },
    methods: {
        OSMS_MaterialIndex() {
            this._api.OSMS_MaterialIndex({ FGroupNo: 3 }).then(res => {
                if (res) {
                    this.materialList = res.data
                }
            })
        },
        chooseItem(item) {
            // this.$u.confirm(`请确认选择 [ ${item.FName} ]`).then(() => {
            window.temFun(item)
            this.$router.back()
            // }).catch(() => { })
        }
    },
    mounted() {
        this.OSMS_MaterialIndex()
    }

};
</script>

<style lang='scss' scoped>
.orgs {
    width: 100%;
    height: 44px;
    border-bottom: 1px solid #f5f6f7;
    border-top: 1px solid #f5f6f7;
    padding: 0 15px;
}

.persons {
    width: 100%;
    height: calc(100% - 84px);
    padding: 0 15px;

    .persons-line {
        display: flex;
        align-items: center;
        width: 100%;
        height: 44px;
        border-bottom: 1px solid #f5f6f7;
        white-space: nowrap;
        overflow: auto;

        .orgname {
            font-size: 14px;
        }
    }
}
</style>
